import { createContext, useEffect, useState } from "react";
import {
  GoogleAuthProvider,
  createUserWithEmailAndPassword,
  onAuthStateChanged,
  signInWithEmailAndPassword,
  signInWithPopup,
  signOut,
  updateProfile,
} from "firebase/auth";
import { auth } from "../auth/firebase";
import { useNavigate } from "react-router-dom";
import { toastErrorNotify, toastSuccessNotify } from "../helpers/Toastify";
export const AuthContext = createContext();

//* with custom hook
// export const useAuthCotext =() => {
//     return useContext(AuthContext)
// }

const AuthContextProvider = ({ children }) => {
  const [currentUser, setCurrentUser] = useState(false);
  let navigate = useNavigate();
  useEffect(() => {
    userObserver();
  }, []);
  const createUser = async (email, password, displayName) => {
    //yeni bir kullanc olusturmak cn kullanlan firebase metodu.. email ve paswordu register sayfasnda kullanyoruz. valueslar burda olustururup contex.provider dan gonderyoruz. register sayfasnda cagryoruz

    try {
      let userCredential = await createUserWithEmailAndPassword(
        auth,
        email,
        password
      );
      //kullanc profl guncellemek cn kullanlan firebase metodu
      await updateProfile(auth.currentUser, {
        displayName,
      });
      console.log(userCredential);
      navigate("/");
      toastSuccessNotify("Registered succesfully");
    } catch (error) {
      toastErrorNotify(error.message);
    }
  };
  const signIn = async (email, password) => {
    try {
      //? mevcut kullancnn giri yapmas iin kullanlan firebase metodu
      let userCredential = await signInWithEmailAndPassword(
        auth,
        email,
        password
      );
      navigate("/");
      toastSuccessNotify("Logged in successfully");
      console.log(userCredential);
    } catch (error) {
      toastErrorNotify(error.message);
    }
  };

  const logOut = () => {
    signOut(auth);
    toastSuccessNotify("Logged out succesfully");
  };
  const userObserver = () => {
    //? Kullancnn signin olup olmadn takip eden ve kullanc deitiinde yeni kullancy response olarak dnen firebase metodu
    onAuthStateChanged(auth, (user) => {
      if (user) {
        const { email, displayName, photoURL } = user;
        setCurrentUser({ email, displayName, photoURL });
        console.log(user);
      } else {
        // User is signed out
        setCurrentUser(false);
        console.log("logged out");
      }
    });
  };

  //* https://console.firebase.google.com/
  //* => Authentication => sign-in-method => enable Google
  //! Google ile girii enable yap
  //* => Authentication => settings => Authorized domains => add domain
  //! Projeyi deploy ettikten sonra google sign-in almas iin domain listesine deploy linkini ekle
  const signUpProvider = () => {
    //? Google ile giri yaplmas iin kullanlan firebase metodu
    const provider = new GoogleAuthProvider();
    //? Alr pencere ile giri yaplmas iin kullanlan firebase metodu
    signInWithPopup(auth, provider)
      .then((result) => {
        console.log(result);
        navigate("/");
      })
      .catch((error) => {
        // Handle Errors here.
        console.log(error);
      });
  };

  const values = { createUser, signIn, logOut, currentUser, signUpProvider };
  return <AuthContext.Provider value={values}>{children}</AuthContext.Provider>;
};
export default AuthContextProvider;
